import React, { useState } from 'react';
import classes from './ModalCard.module.css';

const ModalCard = ({ data, changeModalCompleted }) => {
    const [selected, setSelected] = useState(false);
    const [text, setText] = useState(data.minprice.toString());

    const submithandle =() => {
        if (text === '' || parseInt(text) < data.minprice) {
            return 
        } else {
            changeModalCompleted()
        }
    }

    const select = () => {
        setSelected(!selected);
    };

    return (
        <div className={selected ? classes.cardSelected : classes.card}>
            <div className={classes.top}>
                <div className={classes.check} onClick={select}>
                    <div className={selected ? classes.checkInner : ''}></div>
                </div>

                <div className={classes.second}>
                    <h2>{data.title}</h2>
                    <h3>{data.secondary}</h3>
                </div>

                <div className="hide-mobile">
                    <h5>{data.itemsLeft}</h5> left
                </div>
            </div>

            <p>{data.promotion}</p>
            <div className="hide-desktop">
                <h5>{data.itemsLeft}</h5> left
            </div>

            {selected && (
                <div className={classes.bottom}>
                    {data.empty ? (
                        <button
                            className={classes.empty}
                            onClick={changeModalCompleted}
                        >
                            Continue
                        </button>
                    ) : (
                        <>
                            <p>Enter your pledge</p>
                            <div className="">
                                <input
                                    type="text"
                                    className={
                                        parseFloat(text) < data.minprice
                                            ? classes.error
                                            : ''
                                    }
                                    onChange={(e) => setText(e.target.value)}
                                    value={text}
                                />
                                <button onClick={submithandle}>
                                    Continue
                                </button>
                                <span className={classes.dollar}>$</span>
                            </div>
                        </>
                    )}
                </div>
            )}
        </div>
    );
};

export default ModalCard;
