import React from 'react';
import Button from '../Button/Button';
import classes from './Card.module.css';

const Card = ({ data, changeModalState }) => {
    return (
        <div className={data.itemsLeft > 0 ? classes.card : classes.cardblured}>
            <div>
                <h3>{data.title}</h3>
                <h4 className={classes.secondary}>{data.secondary}</h4>
            </div>

            <p>{data.promotion}</p>

            <div className={classes.bottom}>
                <div>
                    <h2>{data.itemsLeft}</h2>
                    <span>left</span>
                </div>

                {data.itemsLeft > 0 ? (
                    <Button
                        type="sm"
                        text="Select reward"
                        changeModalState={changeModalState}
                    />
                ) : (
                    <Button
                        type="op"
                        text="Out of stock"
                        changeModalState={changeModalState}
                    />
                )}
            </div>
        </div>
    );
};

export default Card;
