import React from 'react';
import classes from './ModalCompleted.module.css';
import { ReactComponent as IconCheck } from '../../assets/icon-check.svg';

const ModalCompleted = ({ changeModalCompleted }) => {
    
    return (
        <>
            <div className={classes.modal}></div>
            <div className={classes.completed}>
                <div>
                    <IconCheck />
                    <h2>Thanks for your support</h2>
                    <p>
                        Your pledge brings us one step closer to sharing
                        Mastercraft bamboo Monitor Riser worldwide. You will get
                        an emial once our campaign is completed.
                    </p>

                    <button onClick={changeModalCompleted}>Got it!</button>
                </div>
            </div>
        </>
    );
};

export default ModalCompleted;
