import React from 'react';
import ModalCard from '../../components/ModalCard/ModalCard';
import { ReactComponent as CloseModal } from '../../assets/icon-close-modal.svg';
import classes from './ModalDeafaul.module.css';

const ModalDefault = ({ changeModalState, changeModalCompleted }) => {
    const datas = [
        {
            title: 'Pledge with no reward',
            secondary: '',
            minprice: '',
            itemsLeft: '',
            empty: true,
            promotion:
                'Choose to support us without a reward if you simply believe in our project. As a backer, you will be signed up to recieve product updates via emial',
        },
        {
            title: 'Bamboo Stand',
            secondary: 'Pledge $25 or more',
            minprice: 25,
            itemsLeft: 101,
            empty: false,
            promotion:
                "You get an ergonomic stand made of natural bamboo. You've helped us launch our promotion campaign, and you'll be added tpo a special Backer member list.",
        },
        {
            title: 'Black Edition Stand',
            secondary: 'Pledge $75 or more',
            minprice: 75,
            itemsLeft: 64,
            empty: false,
            promotion:
                "You get a Black Special Edition computer stand and a personal thank you. You'll ba added to our Backer member list. Shipping is included.",
        },
        {
            title: 'Mahogany Special Edition',
            secondary: 'Pledge $200 or more',
            minprice: 200,
            itemsLeft: 0,
            empty: false,
            promotion:
                "You get two Special Mahogany stands, a backer T-Shirt and a personal thank you. You'll be added to our Backer member list. Shipping is included.",
        },
    ];

    
    return (
        <>
            <div className={classes.modalBefore}></div>
            <div className={classes.modal} id='modal'>
                <div className={classes.header}>
                    <h2>Back this project</h2>
                    <CloseModal
                        className={classes.icon}
                        onClick={changeModalState}
                    />
                </div>
                {datas.map((data, index) => (
                    <ModalCard
                        key={index}
                        data={data}
                        changeModalCompleted={changeModalCompleted}
                    />
                ))}
            </div>
        </>
    );
};

export default ModalDefault;
