import React from 'react';
import classes from './About.module.css';
import Card from '../../components/Card/Card'

const About = ({ changeModalState}) => {
    const datas = [
        {
            title: 'Bamboo Stand',
            secondary: 'Pledge $25 or more',
            minprince: 25,
            itemsLeft: 101,
            promotion:
                "You get an ergonomic stand made of natural bamboo. You've helped us launch our promotion campaign, and you'll be added tpo a special Backer member list.",
        },
        {
            title: 'Black Edition Stand',
            secondary: 'Pledge $75 or more',
            minprince: 75,
            itemsLeft: 64,
            promotion:
                "You get a Black Special Edition computer stand and a personal thank you. You'll ba added to our Backer member list. Shipping is included.",
        },
        {
            title: 'Mahogany Special Edition',
            secondary: 'Pledge $200 or more',
            minprince: 200,
            itemsLeft: 0,
            promotion:
                "You get two Special Mahogany stands, a backer T-Shirt and a personal thank you. You'll be added to our Backer member list. Shipping is included.",
        },
    ];

    return (
        <div className={classes.about}>
            <h3>About this project</h3>
            <p>
                The Mastercraft Bamboo Monitor Riser is a sturdy and stylish
                platform that elevates your screen to a more comfortable viewing
                height. Placing your monitor at your eye level has the potential
                to improve your posture and make you more comfortable while at
                work, helping you stay focused on the task at hand.
            </p>

            <p>
                Featuring artisan craftsmanship, the simplicity of design
                creates extra desk space below your computer to allow notepads,
                pens, and USB sticks to be stored under the stand.
            </p>

            {datas.map((data, index) => {
                return (
                    <Card
                        key={index}
                        data={data}
                        changeModalState={changeModalState}
                    />
                );
            })}
        </div>
    );
};

export default About;
