import Bookmark from './components/Bookmarkcomponent/Bookmark';
import Header from './layouts/Header/Header';
import Main from './layouts/Main/Main';
import Stats from './components/Stats/Stats';
import About from './layouts/About/About';
import ModalDefault from './layouts/ModalDefault/ModalDefault';
import { useState } from 'react';
import ModalCompleted from './components/ModalCompleted/ModalCompleted';

function desktop() {
    document.getElementById('modal').scrollIntoView();
}

function App() {
    const [modalDeafult, setModalDeafult] = useState(false);
    const [modalCompleted, setModalCompleted] = useState(false);

    const changeModalCompleted = () => {
        setModalCompleted(!modalCompleted);
        setModalDeafult(false);
    };

    const changeModalState = () => {
        console.log(modalDeafult);
        setModalDeafult(!modalDeafult);

        if (modalDeafult === true) {
            console.log('Ok');
            desktop();
        }

        console.log(modalDeafult);
    };

    return (
        <div className="App">
            <Header />
            <Main>
                {modalDeafult && (
                    <ModalDefault
                        changeModalState={changeModalState}
                        changeModalCompleted={changeModalCompleted}
                    />
                )}
                <Bookmark changeModalState={changeModalState} />
                <Stats />
                <About changeModalState={changeModalState} />
            </Main>
            {modalCompleted && (
                <ModalCompleted changeModalCompleted={changeModalCompleted} />
            )}
        </div>
    );
}

export default App;
