import React from 'react';
import classes from '../Button/Button.module.css';

const Button = ({ type, text, changeModalState }) => {
    if (type === 'lg') {
        return (
            <span className={classes.btnlg} onClick={changeModalState}>
                {text}
            </span>
        );
    } else if (type === 'op') {
        return <span className={classes.btnop}>{text}</span>;
    }

    return <span className={classes.btn} onClick={changeModalState}>{text}</span>;
};

export default Button;
