import React from 'react'
import { ReactComponent as MasterCraft } from '../../assets/logo-mastercraft.svg'
import { ReactComponent as IconBookmark } from '../../assets/icon-bookmark.svg'
import Button from '../Button/Button';
import classes from '../Bookmarkcomponent/Bookmark.module.css'

const Bookmark = ({ changeModalState }) => {
    return (
        <div className={classes.bookmarkcard}>
            <MasterCraft className={classes.mastercraft} />
            <h2>Mastercarft Bamboo Monitor Raiser </h2>
            <p>
                A beatifully handcrafted monitor stand to reduce nect and eye
                strain
            </p>

            <div className={classes.firstoftype}>
                <Button type="lg" text="Back this project" changeModalState={changeModalState}/>
                <div>
                    <span className={classes.bookmark}>
                        <IconBookmark className={classes.icon} />{' '}
                        <span>Bookmark</span>
                    </span>
                </div>
            </div>
        </div>
    );
};

export default Bookmark

